// @flow
// libs
import React, { memo, useState } from 'react';
import domtoimage from 'dom-to-image';
import fileDownload from 'js-file-download';
import ModalPopup from 'commons/components/Modal';
import Button from 'commons/components/Button';
import moment from 'moment';

type Props = {
  text?: string,
  idWrapElement: string,
};

export const ButtonDownIMG = ({
  text = 'Trend  ',
  idWrapElement = 'groupChart',
}: Props) => {
  const nameImage = `graph_${moment(new Date()).format(
    'YYYY_MM_DD_HH_mm_ss'
  )}.png`;
  const [showModalConfirm, setShowModalConfirm] = useState({
    isShow: false,
    content: '',
  });
  const handleDownloadTrend = () => {
    if (window.navigator && window.navigator.msSaveBlob) {
      setShowModalConfirm({
        ...showModalConfirm,
        isShow: true,
        content:
          '     \n   . ',
      });
    } else {
      domtoimage
        .toBlob(document.getElementById(idWrapElement))
        .then(function (blob) {
          fileDownload(blob, nameImage);
        })
        .catch(function (error) {
          setShowModalConfirm({
            ...showModalConfirm,
            isShow: true,
            content: '   ',
          });
          console.error('oops, something went wrong!', error);
        });
    }
  };

  return (
    <>
      <Button onClick={() => handleDownloadTrend()} customClass="mr-2">
        {text}
      </Button>
      <ModalPopup
        isOpen={showModalConfirm.isShow}
        isShowHeader
        title=""
        isShowIconClose
        isShowFooter
        handleCloseIcon={() => {
          setShowModalConfirm({
            ...showModalConfirm,
            isShow: false,
            content: '',
          });
        }}
        handleClose={() => {
          setShowModalConfirm({
            ...showModalConfirm,
            isShow: false,
            content: '',
          });
        }}
        textBtnLeft=""
        handleSubmit={() => {
          setShowModalConfirm({
            ...showModalConfirm,
            isShow: false,
            content: '',
          });
        }}
      >
        {showModalConfirm.content}
      </ModalPopup>
    </>
  );
};

ButtonDownIMG.defaultProps = {
  idWrapElement: 'groupChart',
  text: 'Trend  ',
};

export default memo<Props>(ButtonDownIMG);
